import _ from "lodash";
import {PRECISION} from "@/lib/common";

/**
 * 点p围绕o点旋转 angle 角度后的坐标值
 * @param p
 * @param o
 * @param angle 弧度
 */
export function transformRotate(p: [number, number], o: [number, number], angle: number) {
    let x = _.round((p[0] - o[0]) * Math.cos(angle) - (p[1] - o[1]) * Math.sin(angle) + o[0], PRECISION);
    let y = _.round((p[0] - o[0]) * Math.sin(angle) + (p[1] - o[1]) * Math.cos(angle) + o[1], PRECISION);
    return [x, y];
}
