import _ from "lodash";
import Topology from "@/lib/core/topo";
import LaserMap from "@/lib/core/laser-map";
let maxLength = 200;    //缩略图宽和高的最大值
let barRange = 50;   //上面和左边的工具栏宽度或高度
let navWidth = 0;   //左边导航栏宽度

class thumbnailCanvas {
    private ctx: CanvasRenderingContext2D | null = null;    //画笔
    private surplusInfo: { width: number, height: number, originX: number, originY: number, mapSize: { width: number, height: number }, mapRange: { minposX: number, maxposY: number } };   //不变的数据
    private maxLength: number = maxLength;   //宽和高的最大值
    private scale = 1; //倍数
    private width = 0; //canvas宽高
    private height = 0;
    private provisionalCanvas: HTMLElement | null = null;   //临时canvas
    static instance: thumbnailCanvas;   //方便外部引用new后的对象
    private thumbnailSolidRect: HTMLElement | null = null;   //实线定位框
    private isDrag: Boolean = false;   //是否正在拖拽
    private lineWidth = 6;   //点位半径
    constructor(surplusInfo: any) {
        this.surplusInfo = surplusInfo;   //不变的数据
        thumbnailCanvas.instance = this;
        this.init()
    }
    init() {
        // 获取地图的大小和范围配置
        let { width, height, originX, originY } = this.surplusInfo;
        this.surplusInfo.mapSize = {
            width: Math.ceil(width * 20),
            height: Math.ceil(height * 20),
        };
        this.surplusInfo.mapRange = {
            minposX: Math.ceil(originX * 20),
            maxposY: Math.ceil((height * 20 + originY * 20)),
        };
        // 获取缩略图的宽高
        let canvasWidth = 0, canvasHeight = 0;
        let canvas: any = document.getElementById("thumbnail");
        let topoWidth = Topology.instance.width, topoHeight = Topology.instance.height, topoScale = Topology.instance.scale;
        let laserWidth = LaserMap.instance.canvas.width - barRange, laserHeight = LaserMap.instance.canvas.height - barRange;
        // 比较地图的宽和高;与大图相同，比例相对高的布满
        if (topoWidth / topoHeight > laserWidth / laserHeight) {
            canvasWidth = this.maxLength;
            canvasHeight = _.round(topoHeight * canvasWidth / topoWidth, 2);
            this.scale = topoScale * canvasWidth / laserWidth
        } else {
            canvasHeight = this.maxLength;
            canvasWidth = _.round(topoWidth * canvasHeight / topoHeight, 2)
            this.scale = topoScale * canvasHeight / laserHeight
        }
        canvas.width = canvasWidth;
        canvas.height = canvasHeight;
        this.ctx = canvas.getContext("2d");
        this.width = canvasWidth;
        this.height = canvasHeight;
        this.lineWidth /= topoScale;
        this.createCanvas()
        this.render();
        this.thumbnailSolidRect = document.getElementById("thumbnailSolidRect");
        this.changePositionRect()
        let parentEle: any = document.getElementsByClassName("thumbnail")[0]
        // // @ts-ignore
        parentEle.style.height = canvasHeight + "px";
        // 添加事件监听--手动移动定位框时，作出反应；当定位框移出时，取消反应
        parentEle.onmousemove = (e: Event) => {
            e.stopPropagation();
            parentEle.oncontextmenu = () => {
                return false
            };
            if (this.isDrag) this.mouseHandler(e, parentEle.offsetLeft, parentEle.offsetTop)
        }
        parentEle.onmousedown = (e: Event) => {
            e.stopPropagation();
            e.preventDefault();
            this.isDrag = true
            this.mouseHandler(e, parentEle.offsetLeft, parentEle.offsetTop)
        }
        parentEle.onmouseup = (e: Event) => {
            e.stopPropagation();
            this.isDrag = false
        }
        parentEle.onmouseleave = (e: Event) => {
            e.stopPropagation();
            this.isDrag = false;
        }
    }
    render() {
        this.ctx.setTransform(1, 0, 0, 1, 0, 0);
        this.ctx.clearRect(0, 0, this.width, this.height); //清除画布
        // 利用canvas画置信度，从而获取底图
        let canvas: any = this.provisionalCanvas;
        let { pgmWidth, pgmHeight, pgmContent } = LaserMap.instance.getLaserData()
        canvas.width = pgmWidth;
        canvas.height = pgmHeight;
        canvas.ctx.putImageData(pgmContent, 0, 0);
        this.ctx.drawImage(canvas, 0, 0, this.width, this.height)
        this.ctx.setTransform(this.scale, 0, 0, this.scale, this.width / 2, this.height / 2);
        this.mapRoutes()
    }
    // 绘制路径
    mapRoutes() {
        this.ctx.lineCap = 'round'
        this.ctx.lineJoin = 'round'
        this.ctx.strokeStyle = '#2b85e495'
        this.ctx.lineWidth = this.lineWidth
        // 优化方向  窗口内数据过滤
        let spots = Topology.instance.spotsMapper;
        let routes = Topology.instance.routesMapper;
        this.ctx.beginPath()
        routes.forEach((val) => {
            let startSpot = spots.get(val.startSpotRefId)
            let endSpot = spots.get(val.endSpotRefId)
            if (startSpot && endSpot) {
                const sx = this.rePosX(startSpot._x * 20), sy = this.rePosY(startSpot._y * 20)
                const ex = this.rePosX(endSpot._x * 20), ey = this.rePosY(endSpot._y * 20)
                this.ctx.moveTo(sx, sy)
                this.ctx.lineTo(ex, ey)
            }
        });
        this.ctx.stroke()
        this.ctx.closePath()
    }
    // 窗口中的相对坐标
    rePosX(x: number) {
        x = x || 0;
        return _.round(x - this.surplusInfo.mapRange.minposX - this.surplusInfo.mapSize.width / 2, 1);
    }
    rePosY(y: number) {
        y = y || 0;
        return _.round(this.surplusInfo.mapRange.maxposY - y - this.surplusInfo.mapSize.height / 2, 1);
    }
    // 创建临时canvas，用于绘制底图
    createCanvas() {
        let canvas: any = document.createElement("CANVAS");
        canvas.style.display = "none";
        document.body.appendChild(canvas);
        canvas.ctx = canvas.getContext("2d");
        this.provisionalCanvas = canvas;
    }
    // 大地图缩放和移动时，小地图实时变化
    changePositionRect(params?: { left: number, top: number }) {
        let left: number, top: number;
        let changeScale = Topology.instance.scale / this.scale;
        let width = (LaserMap.instance.canvas.width - barRange) / changeScale;
        let height = (LaserMap.instance.canvas.height - barRange) / changeScale;
        if (params) {
            // 有params传入，代表是手动移动定位框
            left = params.left - width / 2; //点击的位置往左移一半
            top = params.top - height / 2;
            let gx = - left * changeScale + barRange;   // 计算大图左上角距离父元素左上角的距离
            let gy = - top * changeScale + barRange;
            // 使大图跟着重新绘制
            Topology.instance.draw.move(gx, gy);
            Topology.instance.gx = gx;
            Topology.instance.gy = gy;
            LaserMap.instance.render()
        } else {
            // 没有params，代表大图缩放或者偏移时，定位框随着变化
            left = - (Topology.instance.gx - barRange) / changeScale;
            top = - (Topology.instance.gy - barRange) / changeScale;
        }
        // 定位框不能超出缩略图的范围，因此作出截取
        if (left < 0) {
            width += left;
            if (width > this.width) width = this.width;
            left = 0;
        } else {
            if (width + left > this.width) width = this.width - left
        }
        if (top < 0) {
            height += top;
            top = 0;
            if (height > this.height) height = this.height;
        } else {
            if (height + top > this.height) height = this.height - top
        }
        // 修改定位框的样式，超出父元素时，隐藏
        if (width < 0 || height < 0) {
            this.thumbnailSolidRect.style.opacity = "0"
        } else {
            this.thumbnailSolidRect.style.opacity = "1"
        }
        this.thumbnailSolidRect.style.width = width + "px";
        this.thumbnailSolidRect.style.height = height + "px";
        this.thumbnailSolidRect.style.left = left + "px";
        this.thumbnailSolidRect.style.top = top + "px";
    }
    // 手动移动定位框
    mouseHandler(e: Event | any, offsetLeft: number, offsetTop: number) {
        let left = e.pageX - navWidth - offsetLeft
        let top = e.pageY - offsetTop;
        this.changePositionRect({ left, top })
    }
}

export default thumbnailCanvas