import {PRECISION} from "@/lib/common";
import _ from 'lodash';

/**
 * 判断两条线段时候相交
 * @param line1 [x1,y1,x2,y2]
 * line1 是生成矩形的对角线，因此不能垂直或者平行，否则默认不相交， 返回 false
 * @param line2 [x1,y1,x2,y2]
 */
const lineIntersect = (line1: [number, number, number, number] = [0, 0, 0, 0], line2: [number, number, number, number] = [0, 0, 0, 0]) => {
    if (line1[0] === line1[2] || line1[1] === line1[3]) {
        return false
    }
    const k1 = _.round((line1[1] - line1[3]) / (line1[0] - line1[2]), PRECISION),
        b1 = _.round((line1[0] * line1[3] - line1[2] * line1[1]) / (line1[0] - line1[2]), PRECISION);
    /**
     * line2 存在 垂直 or 平行 的情况， 需要特殊处理
     * 根据情况 只需要处理 分母为零 的情况 即 垂直
     */
    if (line2[0] === line2[2]) {
        /**
         * trick
         */
        line2[0] += 0.05;
    }
    const k2 = _.round((line2[1] - line2[3]) / (line2[0] - line2[2]), PRECISION);
    const b2 = _.round((line2[0] * line2[3] - line2[2] * line2[1]) / (line2[0] - line2[2]), PRECISION);
    if (k1 !== k2) {
        const x = (b2 - b1) / (k1 - k2);
        if ((x - line1[0]) * (x - line1[2]) < 0 && (x - line2[0]) * (x - line2[2]) < 0) {
            return true
        }
    }
    return false
};

export default lineIntersect
