/**
 * 点位的可扩展属性模版
 */
// @ts-ignore
const { SpotLabelEnum, SpotTypeEnum, OrientEnum, property4Enum } = window.enumerationList

export interface CustomSpotInterface {
    codePrefix: string;
    codeSuffix: string;
    // aliasPrefix: string;
    // aliasSuffix: string;
    dir: number;
    type: number[];
    orient: number[];
}


class CustomSpot {
    // 是否显示
    visible: boolean = false;
    labels = SpotLabelEnum;
    // 保持 key-value 的一致
    childLabels = {
        type: SpotTypeEnum,
        orient: OrientEnum,
        property4: property4Enum,
    };

    /**
     * 预定义值
     */
    predefineTemplate: CustomSpotInterface = {
        codePrefix: 'Goal',
        codeSuffix: '',
        // aliasPrefix: 'Goal',
        // aliasSuffix: '',
        dir: 0,
        type: [0],
        // layingOff: false,
        orient: [7],    //默认值由 “左” 改为 “不指定”
        // offset: 0,
        // offset_x: 0,
        // offset_y: 0,
        // offset_theta: 0,
        // reloc_weight: 0,
        // is_traffic: false,
        // is_forbidden: false,
    };

    constructor() {
    }
}

export default CustomSpot

