/**
 * 路径的可扩展属性模版
 */
// @ts-ignore
const { RouteLabelEnum, PointModeEnum, Property2Enum, Property3Enum } = window.enumerationList

export interface CustomRouteInterface {
    // refId: number;
    maxSpeed: number;
    // securityDist: number;
    pointMode: number[];
    // isAvoidance: boolean;
    // avoidanceMode: number;
    // expansionMode: number;
    property2: number[];
    property3: number[];
    property5: boolean;
    fullWeight: boolean;
}


class CustomRoute {
    // 是否显示
    visible: boolean = false;
    labels = RouteLabelEnum;
    // 保持 key-value 的一致
    childLabels = {
        pointMode: PointModeEnum,
        property2: Property2Enum,
        property3: Property3Enum
    };

    /**
     * 预定义值
     */
    predefineTemplate: CustomRouteInterface = {
        // refId: 0,
        maxSpeed: 0,
        // securityDist: 0,
        pointMode: [0],
        // isAvoidance: false,
        // avoidanceMode: 0,
        // expansionMode: 0,
        property2: [0],
        property3: [1], //默认从圆弧改为直行
        property5: false,
        fullWeight: false
    };

    constructor() {
    }
}

export default CustomRoute

