import _ from "lodash";

interface LineAngle {
    readonly x1: number;
    readonly y1: number;
    readonly x2: number;
    readonly y2: number;
}

const PI = 3.14159265;

export function lineAngle(segment: LineAngle): number {
    let da;
    let dir = 0;

    const dx = segment.x2 - segment.x1;
    const dy = segment.y2 - segment.y1;

    if (dx === 0) {
        if (dy > 0) {
            dir = 0;
        } else {
            dir = 180;
        }
    } else {
        if (dx > 0) {
            if (dy === 0) {
                dir = 90;
            } else {
                if (dy > 0) {
                    da = Math.abs(dx / dy);
                    dir = Math.atan(da) * 180 / PI;
                } else {
                    if (dy < 0) {
                        da = Math.abs(dx / dy);
                        dir = Math.atan(da) * 180 / PI;
                        dir = 180 - dir;
                    }
                }
            }
        } else {
            if (dy === 0) {
                dir = 270;
            } else {
                if (dy >= 0) {
                    da = Math.abs(dx / dy);
                    dir = Math.atan(da) * 180 / PI;
                    dir = 360 - dir;
                } else {
                    da = Math.abs(dx / dy);
                    dir = Math.atan(da) * 180 / PI;
                    dir = 180 + dir;
                }
            }
        }
    }
    return _.round(dir * PI / 180, 3);
}

// 转化角度，控制范围：-180到180度
export const handleAngleRange = (angle: number, PRECISION = 1) => {
    if (!angle && angle !== 0) return 180;
    angle = _.round(angle % 360, PRECISION);
    if (angle > 180) { angle -= 360 } else if (angle < -180) { angle += 360 }
    return angle
}