<template>
  <div class="map-editor">
    <map-editor :isRcs="isRcs" />
    <el-page-header class="editor-back" @back="goBack" v-if="isRcs">
    </el-page-header>
  </div>
</template>

<script>
import mapEditor from "@/components/MapEditor";
export default {
  components: { mapEditor },
  data() {
    return {
      isRcs: false, //是否是rcs系统
      backPage: "/", //返回地址
    };
  },
  mounted() {
    this.isRcs = this.$route.path === "/mapEditor";
    this.backPage = this.$route.query.backPage || "/";
  },
  methods: {
    // 返回来源页面
    goBack() {
      this.$router.push(this.backPage);
    },
  },
};
</script>

<style lang="scss" scoped>
.map-editor {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.editor-back {
  position: absolute;
  top: 13px;
  right: -30px;
  color: azure;
}
</style>