import _ from "lodash";
import { handleAngleRange } from "@/lib/utils/handle-angle";
import { PRECISION } from "@/lib/common";
import Topology from "@/lib/core/topo";
/**
 * 判断是否是JSON字符串
 */
export const isJson = (str: any) => {
  if (isNaN(Number(str))) {
    try {
      JSON.parse(str);
      return true;
    } catch (e) {
      return false;
    }
  } else {
    return false;
  }
}
/**
 * 根据不同的key类型，处理批量数据value
 */
export const handleBatchValue = (key: string, value: any) => {
  switch (key) {
    case "dir":
    case "offsetTheta":
    case "property2Theta":
    case "property3Theta":
      value = handleAngleRange(value);
      break;
    case "offsetX":
    case "offsetY":
    case "property2X":
    case "property2Y":
    case "property3X":
    case "property3Y":
    case "maxSpeed":
    case "securityDist":
    case "avoidanceMode":
    case "expansionMode":
      value = _.round(value, PRECISION);
      break;
    case "x":
      value = _.round(value * Topology.instance.SCALE - Topology.instance.ox, PRECISION);
      break;
    case "y":
      value = _.round(
        Topology.instance.oy - value * Topology.instance.SCALE + Topology.instance.height,
        PRECISION
      );
      break;
    case "type":
    case "orient":
    case "pointMode":
    case "property2":
    case "property3":
    case "property4":
      value = value[0];
      break;
    case "property5":
    case "layingOff":
    case "isTraffic":
    case "isForbidden":
    case "isAvoidance":
    case "fullWeight":
      value = Number(value);
      break;
  }
  return value;
}

