/**
 * Event Loop
 * 高频触发优化 延时函数
 * 降低计算频率
 * @param cb 回调函数
 * @param delay  延时参数 ms
 */

// 标志符
let flag = false;
// 定时器
let timer: any | 0 = 0;
const delayFunc = (cb: Function, delay: number = 300) => {
    if (!flag) {
        timer = setTimeout(() => {
            cb();
            flag = false;
            clearTimeout(timer);
        }, delay);
        flag = true;
    }
};
export default delayFunc
