let srcPublic = process.env.BASE_URL + "static/img/" //图标路径公共前缀

// 获取小车状态图标
export const StatusIcon = function (rb: { warnId: number, enabledState: number, status: string }) {
  let robot = srcPublic + 'running.png';
  if (rb.warnId > 0) {
    robot = srcPublic + 'warn.png';
  } else if (rb.enabledState === 2) {
    robot = srcPublic + 'stop.png';
  } else {
    let status = rb.status;
    switch (status) {
      case '充电中':
        robot = srcPublic + 'charge.png';
        break;
      case '空闲':
        robot = srcPublic + 'running.png';
        break;
      case '移动中':
        robot = srcPublic + 'running.png';
        break;
      case '任务等待中':
        robot = srcPublic + 'busy.png';
        break;
      case '充电重试中':
        robot = srcPublic + 'busy.png';
        break;
      case '用户确认中':
        robot = srcPublic + 'busy.png';
        break;
      case '交管请求中':
        robot = srcPublic + 'busy.png';
        break;
      case '任务改派中':
        robot = srcPublic + 'busy.png';
        break;
      case '异常告警中':
        robot = srcPublic + 'warn.png';
        break;
      default:
        robot = srcPublic + 'running.png';
        break
    }
  }
  return robot
};

// 0 默认路径标点
const normal = new Image();
normal.src = srcPublic + 'spot_0.png';

// 1 工位点
const station = new Image();
station.src = srcPublic + 'spot_1.png';
// 2 充电点
const charge = new Image();
charge.src = srcPublic + 'spot_2.png';
// 3 休息点
const rest = new Image();
rest.src = srcPublic + 'spot_3.png';
// 4 码头
const dock = new Image();
dock.src = srcPublic + 'spot_4.png';
// 5 滚筒点
const roller = new Image();
roller.src = srcPublic + 'spot_5.png';

// 6 充电桩
let stationList = (state?: number) => {
  let station = new Image();
  switch (state) {
    case 1:
      station.src = srcPublic + 'point-station-reserve.png'
      break;
    case 2:
      station.src = srcPublic + 'point-station-using.png'
      break;
    default:
      station.src = srcPublic + 'point-station-normal.png'
      break;
  }
  return station
}

/*
~~~~ 外部设备 ~~~
*/
// 1 电梯
const elevator = new Image();
elevator.src = srcPublic + 'external_1.png';
// 2 自动门
const movable = new Image();
movable.src = srcPublic + 'external_2.png';
// 3 提升机
const lifter = new Image();
lifter.src = srcPublic + 'external_3.png';
// 0 默认图标
const defaultExternal = new Image();
defaultExternal.src = srcPublic + 'external_0.png';

// 点位图标集合
const SpotIconImg = new Map().set(0, normal).set(1, station).set(2, charge).set(3, rest).set(4, dock).set(5, roller).set(6, stationList)
// 点位图标集合
const externalIconImg = new Map().set(1, elevator).set(2, movable).set(3, lifter)

// 获取点位图标
export const getSpotImg = (spot: any) => {
  if (spot.externalType !== undefined) {
    let img = externalIconImg.get(spot.externalType)
    if (img) {
      return img
    } else {
      return defaultExternal
    }
  } else {
    if (SpotIconImg.get(spot.type)) {
      if (spot.type === 6) {
        // 充电桩类型点位
        return SpotIconImg.get(6)(spot.useState)
      } else {
        // 其他类型点位
        return SpotIconImg.get(spot.type)
      }
    } else {
      // 默认使用路径标点
      return SpotIconImg.get(0)
    }
  }
}

// 点位方向箭头
const ARROWARC = new Image()  //转弯方式为转弯
ARROWARC.src = srcPublic + 'arrow_arc.png';
const ARROWNORMAL = new Image() //转弯方式为其他
ARROWNORMAL.src = srcPublic + 'arrow.png';
// 根据路径转弯方式返回不同的箭头
export const ARROW = (property3: number | string) => {
  if (Number(property3) === 0) {
    return ARROWARC
  } else {
    return ARROWNORMAL
  }
}