import _ from "lodash";
import { PRECISION } from "@/lib/common";
import Topology from "@/lib/core/topo";
import Route from "@/lib/core/route";
// @ts-ignore
const { RouteLabelEnum, PointModeEnum, Property2Enum, Property3Enum } = window.enumerationList

export interface EditRouteInterface {
    type: number;
    pointMode: number[];
    isAvoidance: boolean;
    fullWeight: boolean;
    expansionMode: number;
    avoidanceMode: number;
    refId: number;
    startSpotRefId: number;
    endSpotRefId: number;
    maxSpeed: number;
    property1: string;
    property2: number[];
    property3: number[];
    property4: string;
    property5: boolean;
    securityDist: number;
    walkConfidence: number;
    walkMode: number;
}

class EditRoute {
    // 是否显示
    visible: boolean = false;
    readonly labels = RouteLabelEnum;
    // 保持 key-value 的一致
    readonly childLabels = {
        pointMode: PointModeEnum,
        property2: Property2Enum,
        property3: Property3Enum
    };

    // 考虑添加过滤器实现 避免定义过多的数据副本

    /**
     * 预定义值
     */
    propertyTemplate: EditRouteInterface = {
        refId: 0,
        startSpotRefId: 0,
        endSpotRefId: 0,
        maxSpeed: 0,
        securityDist: 0,
        pointMode: [0],
        isAvoidance: false,
        avoidanceMode: 0,
        expansionMode: 0,
        property1: '',
        property2: [0],
        property3: [1], //默认从圆弧改为直行
        property4: '',
        property5: false,
        fullWeight: false,
        walkConfidence: 0,
        walkMode: 0,
        type: 0,

    };

    constructor() {
    }
}

export default EditRoute


/**
 * 地图 route 到 route prop 编辑表单的字段映射
 */

export const copyRouteProps = (route: Route) => {
    let props = {};
    for (let k in RouteLabelEnum) {
        // @ts-ignore
        route[k] !== undefined && (props[k] = route[k]);
    }
    // @ts-ignore
    props['pointMode'] = [route['pointMode']];
    // @ts-ignore
    props['property2'] = [route['property2']];
    // @ts-ignore
    props['property3'] = [route['property3']];
    // @ts-ignore
    props['isAvoidance'] = Boolean(route['isAvoidance']);
    // @ts-ignore
    props['property5'] = Boolean(Number(route['property5']));
    // @ts-ignore
    props['fullWeight'] = Boolean(route['fullWeight']);
    // @ts-ignore
    props.extend = route.extend
    return props
};

