import { request } from "@/utils/request";

// 从后台获取地图列表☑️
export function getMapListFromServer() {
    return request.post('editor/getLocalMaps')
}

// 从后台打开地图☑️
export function getMapDetailsFromServer(data: any) {
    return request.post('editor/getServerMapZip', data, "blob")
}
// 将地图保存至后台☑️
export function saveMapToServer(data: any) {
    return request.post('/editor/saveServereMapZip', data, "", "multipart/form-data;charset=UTF-8")
}

// 删除后台地图☑️
export function deleteServerMap(data: any) {
    return request.post('editor/deleteServerMapZip', data)
}

// 从调度获取区域列表☑️
export function getAreaListFromScheduler() {
    return request.post('area/list', {})
}

// 从调度获取地图列表☑️
export function getMapListFromScheduler() {
    return request.get('map/mapList')
}
// 从调度打开地图☑️
export function getMapDetailsFromScheduler(fileName: any, areaId: any, areaName: any) {
    return request.get('map/downloadFile/' + fileName, { areaId, areaName }, "blob")
}
// 将地图保存至调度☑️
export function saveMapToScheduler(data: any, areaId: any, areaName: any) {
    return request.post('map/uploadFile?areaId=' + areaId + '&areaName=' + areaName, data, "", "multipart/form-data;charset=UTF-8")
}

// 修改调度地图 - 启用地图☑️
export function updateSchedulerMap(data: any) {
    return request.post('map/updateMap', data)
}

// 获取外部设备列表
export function getExternalList(data: any) {
    return request.post('/equipment/list', data)
}

// 获取外部设备类型
export function getExternalTypeList() {
    return request.get('/equipment/type')
}