<template>
<!-- 批量修改中的扩展属性、修改某个点位路径的扩展属性参数 -->
  <div :class="{ 'prop-template-content': type === 'prop' }">
    <div
      v-for="(item, key) in extendParse"
      :key="key"
      v-show="item.user === extendUser && key"
    >
      <div
        v-if="item.type !== 'double' || !Array.isArray(item.name)"
        :class="{
          'prop-item': type === 'prop',
          'batch-item': type === 'batch',
        }"
      >
        <b :class="{ 'batch-title': type === 'batch' }"
          >{{ item.name || key }} :
        </b>
        <span v-if="item.type === 'boolean'" class="extend-radio">
          <el-radio v-model="extendForm[key]" :label="1">是</el-radio>
          <el-radio v-model="extendForm[key]" :label="0">否</el-radio>
          <i
            class="el-icon-close close-radio"
            @click="changeBoolean(extendForm, key)"
          ></i>
        </span>
        <span v-if="item.type === 'string'">
          <el-input
            class="prop-form-inline"
            size="mini"
            v-model.trim="extendForm[key]"
          >
          </el-input>
        </span>
        <!-- 单个double -->
        <span v-if="item.type === 'double'">
          <el-input-number
            size="mini"
            class="prop-form-inline"
            v-model="extendForm[key]"
            controls-position="right"
            :max="item.max !== null ? item.max : Infinity"
            :min="item.min !== null ? item.min : -Infinity"
            :precision="item.precision !== undefined ? item.precision : 3"
          >
          </el-input-number>
        </span>
        <!-- 单选或者多选 -->
        <span v-if="item.type === 'int' && item.valueEnum">
          <el-select
            :multiple="typeof item.value === 'object'"
            size="mini"
            class="prop-form-inline"
            v-model="extendForm[key]"
            placeholder="请选择"
            clearable
          >
            <el-option
              v-for="(label, value) in item.valueEnum"
              :key="value"
              :label="label"
              :value="Number(value)"
            >
            </el-option>
          </el-select>
        </span>
        <!-- 单个整数 -->
        <span v-if="item.type === 'int' && !item.valueEnum">
          <el-input-number
            size="mini"
            class="prop-form-inline"
            v-model="extendForm[key]"
            controls-position="right"
            :max="item.max !== null ? item.max : Infinity"
            :min="item.min !== null ? item.min : -Infinity"
            :precision="0"
          >
          </el-input-number>
        </span>
        <el-button
          v-if="type === 'batch'"
          class="batch-btn"
          type="primary"
          @click="handleBatch(key, extendUser + 'Extend', item.name || key)"
          >修改</el-button
        >
      </div>
      <!-- 多个double值 -->
      <div
        v-for="(doubleValue, i) in item['name']"
        :key="i"
        v-else-if="type === 'prop' && extendForm[key]"
        :class="{
          'prop-item': type === 'prop',
          'batch-item': type === 'batch',
        }"
      >
        <b>{{ item.name[i] || key + (i + 1) }} : </b>
        <span>
          <el-input-number
            size="mini"
            class="prop-form-inline"
            v-model="extendForm[key][i]"
            controls-position="right"
            :max="item.max[i] !== null ? item.max[i] : Infinity"
            :min="item.min[i] !== null ? item.min[i] : -Infinity"
            :precision="item.precision !== undefined ? item.precision[i] : 3"
          >
          </el-input-number>
        </span>
      </div>
      <div
        v-for="(doubleValue, i) in extendForm[key]"
        :key="i"
        v-else-if="type === 'batch'"
        class="batch-item"
      >
        <div class="batch-title">{{ item.name[i] || key + (i + 1) }} :</div>
        <span>
          <el-input-number
            size="mini"
            class="prop-form-inline"
            v-model="extendForm[key][i]"
            controls-position="right"
            :max="item.max[i] !== null ? item.max[i] : Infinity"
            :min="item.min[i] !== null ? item.min[i] : -Infinity"
            :precision="item.precision !== undefined ? item.precision[i] : 3"
          >
          </el-input-number>
        </span>
        <el-button
          class="batch-btn"
          type="primary"
          @click="
            handleBatch(
              key,
              extendUser + 'Extend',
              item.name[i] || key + (i + 1)
            )
          "
          >修改</el-button
        >
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["extendUser", "extendParse", "extendForm", "type"],
  methods: {
    changeBoolean(extendForm, key) {
      this.$emit("changeBoolean", extendForm, key);
    },
    handleBatch(key, type, name) {
      this.$emit("handleBatch", key, type, name);
    },
  },
};
</script>

<style lang="sass" scoped>
</style>