import _ from 'lodash';
import SVG from 'svg.js';
import { SpotInterface } from "@/lib/types";
import Topology from "@/lib/core/topo";
import { PRECISION } from "@/lib/common";
import copy from "@/lib/utils/copy";
import { handleAngleRange } from "@/lib/utils/handle-angle"

class Spot {
    /**
     * _x、_y  激光坐标值
     * _dir 弧度值
     */
    _x: number = 0;
    _y: number = 0;
    _dir: number = 0;
    code: string = '';
    public readonly refId: number = 0;

    icon?: SVG.G | null = null;

    private view: Topology = Topology.instance;
    /**
     * 允许空，但需要非空重名校验逻辑
     */
    alias: string = '';
    type: number;
    orient: number;
    description: string;
    offsetTheta: number;
    isForbidden: number;
    property1: string;
    property2: string;
    property3: string;
    property4: number;
    property5: number;
    offset: number;
    offsetX: number;
    offsetY: number;
    isTraffic: number;
    layingOff: number;
    relocWeight: number;
    property2X: number;
    property2Y: number;
    property2Theta: number;
    property3X: number;
    property3Y: number;
    property3Theta: number;
    extend: string;
    externalType: [undefined, number]

    constructor(spot: SpotInterface) {
        this._x = spot.x;
        this._y = spot.y;
        this._dir = spot.dir;
        this.code = spot.code;
        this.alias = spot.alias;
        this.refId = spot.refId;
        this.type = spot.type;
        this.orient = spot.orient;
        this.description = spot.description;
        this.isForbidden = spot.isForbidden;
        this.isTraffic = spot.isTraffic;
        this.layingOff = spot.layingOff;
        this.offset = spot.offset;
        this.offsetTheta = spot.offsetTheta;
        this.offsetX = spot.offsetX;
        this.offsetY = spot.offsetY;
        this.property1 = spot.property1;
        this.property2 = spot.property2;
        this.property3 = spot.property3;
        this.property4 = spot.property4;
        this.property5 = spot.property5;
        this.relocWeight = spot.relocWeight;
        this.property2X = spot.property2X;
        this.property2Y = spot.property2Y;
        this.property2Theta = spot.property2Theta;
        this.property3X = spot.property3X;
        this.property3Y = spot.property3Y;
        this.property3Theta = spot.property3Theta;
        this.extend = spot.extend
        this.externalType = spot.externalType
    }


    /**
     * 属性相关的修改，均属于 更新功能
     */

    /**
     * browser position x
     * @private
     */
    set x(val: number) {
        this._x = _.round((val + this.view.ox) / this.view.SCALE, PRECISION);
    }

    get x(): number {
        return _.round(this._x * this.view.SCALE - this.view.ox, PRECISION);
    }

    /**
     * browser position y
     * @private
     */
    set y(val: number) {
        this._y = _.round((this.view.oy + this.view.height - val) / this.view.SCALE, PRECISION);
    }

    get y(): number {
        return _.round(this.view.oy - this._y * this.view.SCALE + this.view.height, PRECISION);
    }

    /**
     * 保存弧度值
     * browser angle  degree
     * @private
     */
    set dir(val: number) {
        this._dir = _.round(val * Math.PI / 180, PRECISION) || 0;
    }

    /**
     * 显示角度
     * [-180, 180]
     */
    get dir(): number {
        return handleAngleRange(this._dir * 180 / Math.PI) || 0
    }

}

/**
 * update  && delete
 * @param arr
 */
export const getSpotsCopyData = (arr: Spot[]) => {
    return arr.map(spot => {
        // TODO 补全有效字段信息
        return {
            refId: spot.refId,
            x: spot.x, // 激光坐标
            y: spot.y,
            dir: spot.dir,
            code: spot.code,
            alias: spot.alias,
            type: spot.type,
            orient: spot.orient,
            description: spot.description,
            isForbidden: spot.isForbidden,
            isTraffic: spot.isTraffic,
            layingOff: spot.layingOff,
            offset: spot.offset,
            offsetTheta: spot.offsetTheta,
            offsetX: spot.offsetX,
            offsetY: spot.offsetY,
            property1: spot.property1,
            property2: spot.property2,
            property3: spot.property3,
            property4: spot.property4,
            property5: spot.property5,
            relocWeight: spot.relocWeight,
            property2X: spot.property2X,
            property2Y: spot.property2Y,
            property2Theta: spot.property2Theta,
            property3X: spot.property3X,
            property3Y: spot.property3Y,
            property3Theta: spot.property3Theta,
            extend: spot.extend
        }
    })
};

export default Spot
