  mounted(): void {
    //@ts-ignore
    this.Topo = new Topology(document.getElementById("main"));
    /**
     * 激光数据渲染
     */
    // this.LaserMap = new LaserMap(process.env.VUE_APP_BASE_API + '/tool/getPNG?mapid=' + mapid);
    this.LaserMap = new LaserMap();
    /**
     * 开放 MERGE 模板
     */
    this.LaserMap.customMerge.addCustomMergeUI = () => {
      this.mergeVisible = true;
    };

    /**
     * SPOT模板
     */
    this.Topo.addCustomSpot = () => {
      // @ts-ignore
      this.Topo.customSpot.visible = true;
      // @ts-ignore
      this.form = this.Topo.customSpot;
    };

    /**
     * ROUTE 模板
     */
    this.Topo.addCustomRoute = () => {
      // @ts-ignore
      this.Topo.customRoute.visible = true;
      // @ts-ignore
      this.form = this.Topo.customRoute;
    };

    /**
     * SPOT 属性编辑
     */
    this.Topo.doubleClickSpot = (spot: any) => {
      let props = JSON.parse(JSON.stringify(this.Topo.spotProps));
      // @ts-ignore
      props.visible = true;
      // @ts-ignore
      props.propertyTemplate = copySpotProps(spot);
      // @ts-ignore
      this.propForm = props;
      this.keyhiddenList = SportKeyHiddenList;
      this.extendUser = "spot";
      this.extendForm = this.handleExtend(
        this.propForm.propertyTemplate.extend
      );
      this.Topo.clearVisualSpots();
    };

    /**
     * 开放 ROUTE 属性编辑
     */
    this.Topo.doubleClickRoute = (route: any) => {
      let props = JSON.parse(JSON.stringify(this.Topo.routeProps));
      // @ts-ignore
      props.visible = true;
      // @ts-ignore
      props.propertyTemplate = copyRouteProps(route);
      // @ts-ignore
      this.propForm = props;
      this.keyhiddenList = RouteKeyHiddenList;
      this.extendUser = "route";
      this.extendForm = this.handleExtend(
        this.propForm.propertyTemplate.extend
      );
      this.Topo.clearVisualSpots();
    };

    /**
     *同步数据, 保证页面上的操作实时体现在弹框中
     */
    this.Topo.syncSpotPropForm = (spot: any) => {
      if (!this.propForm.propertyTemplate) {
        return;
      } else if (this.propForm.propertyTemplate.startSpotRefId) {
        this.Topo.doubleClickSpot(spot);
      } else {
        // @ts-ignore
        this.propForm.propertyTemplate = copySpotProps(spot);
        this.extendUser = "spot";
        this.extendForm = this.handleExtend(
          this.propForm.propertyTemplate.extend
        );
      }
    };
    this.Topo.syncRoutePropForm = (route: any) => {
      if (!this.propForm.propertyTemplate) {
        return;
      } else if (!this.propForm.propertyTemplate.startSpotRefId) {
        this.Topo.doubleClickRoute(route);
      } else {
        // @ts-ignore
        this.propForm.propertyTemplate = copyRouteProps(route);
        this.extendUser = "route";
        this.extendForm = this.handleExtend(
          this.propForm.propertyTemplate.extend
        );
      }
    };

    /**
     * 填充模板
     */
    this.Topo.setFillRange = () => {
      this.fillRangeCollapse = [];
      this.fillRangeForm = this.Topo.fillRange;
    };
    // 调用外部的下载方法，用于快捷 ctrl+s
    this.Topo.downloadZip = () => {
      this.saveFile("downloadZip");
    };
    // 清空点位选择
    this.Topo.clearMapSelectValue = () => {
      this.mapSelectValue = "";
    };
    this.Topo.closePropTemplate = this.closePropTemplate;
    // this.timeoutControl = new timeoutControl(3); //增加超时计时对象
    // 获取外部设备类型枚举
    getExternalTypeList().then((res) => {
      if (res && res.state) {
        res.data.forEach((item: any) => {
          this.$set(this.Topo.externalTypeEnum, item.id, item.name);
        });
      }
    });
    // 如果从地图列表页或者地图详情页跳转过来，直接打开
    let { fileName, areaId, mapId, areaName } = this.$route.query;
    if (fileName && areaId) {
      this.schedulerMap.selectedMap = mapId;
      this.schedulerCheckedArea = Number(areaId);
      getMapDetailsFromScheduler(fileName, Number(areaId), areaName).then(
        (res: any) => {
          if (!res) return;
          let file = new File([new Blob([res])], fileName + ".zip", {
            type: "application/zip",
          });
          let e = { target: { files: [file] } };
          this.readMapFile(e, "scheduler");
        }
      );
    }
    // 防止页面过期定时器
    this.preventExceed = setInterval(() => {
      getMapListFromServer().catch(() => {
        this.preventExceed && clearInterval(this.preventExceed);
      });
    }, 10 * 60 * 1000);
  }
  beforeDestroy(): void {
    // this.timeoutControl.beforeDestroy();
    // 切换页面前，移除
    window.removeEventListener("resize", this.Topo.topoResize);
    window.removeEventListener("resize", this.LaserMap.laserMapResize);
    this.preventExceed && clearInterval(this.preventExceed);
    this.loadingMessage && this.loadingMessage.close();
    this.Topo.lockingMessage && this.Topo.lockingMessage.close();
    destroyBeginData();
  }
  // 关闭修改点位属性弹框
  closePropTemplate() {
    this.propForm.visible = false;
    this.Topo.clearVisualSpots();
  }

  /**
   * 合并地图钩子
   */
  // 覆盖默认的上传行为
  httpRequest(data: any) {
    this.loadingMessage && this.loadingMessage.close();
    this.loadingMessage = this.$message({
      type: "info",
      duration: 200000,
      message: "正在绘制合并地图中, 请稍后",
    });
    // @ts-ignore
    window.handleZip
      .parseMapFile({ target: { files: [data.file] } }, {}, true)
      .then((res: any) => {
        this.mergeVisible = false;
        this.mergeUploadFileList = [];
        let {
          width,
          height,
          routes: lines,
          spots,
          originX,
          originY,
          pgmContent: pgm,
        } = res;
        let conf = {
          width,
          height,
          lines,
          spots: this.Topo.handleSpotsFromFile(spots),
          originX,
          originY,
          pgm,
        };
        Object.assign(conf, { SCALE: 20 });
        this.LaserMap.customMerge.renderMergeData(conf);
        this.loadingMessage && this.loadingMessage.close();
      })
      .catch((err: string) => {
        Message.error("解析文件失败：" + err);
        this.loadingMessage && this.loadingMessage.close();
      });
  }

  mergeUploadChange(e: Event | any) {
    console.log("upload change", e);
  }

  handleCommand(cmd: string) {
    if (cmd === "openServerFile") {
      this.getServerMapList();
    } else if (cmd === "openLocalFile") {
      let file = document.getElementById("filepicker");
      if (file) file.click();
    } else if (cmd === "openSchedulerFile") {
      this.getSchedulerMapList();
      this.openFromScheduler = true;
    } else if (
      cmd === "downloadPng" ||
      cmd === "downloadZip" ||
      cmd === "saveToServer"
    ) {
      this.saveFile(cmd);
    } else if (cmd === "sendToScheduler") {
      if (this.surplusInfo) {
        this.getSchedulerAreaList();
        this.saveToScheduler = true;
      } else {
        Message.warning("请先选择一张地图并打开");
      }
    } else if (cmd === "updateFileName") {
      if (this.surplusInfo) {
        this.$prompt("请输入新的地图名", "修改地图名称", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          inputPattern: /^[\w-]*[a-zA-Z-_]+[\w-]*$/,
          inputErrorMessage: "地图名由字母、数字、下划线组成, 不能为纯数字",
        })
          .then(async (msg: any) => {
            // @ts-ignore
            if (window.handleZip.updateFileName(msg.value)) {
              Message.success("地图名称修改成功");
              this.serverCheckedMap.id = "";
            } else {
              Message.warning("与旧地图名称相同, 不做修改");
            }
          })
          .catch(() => {});
      } else {
        Message.warning("请先选择一张地图并打开");
      }
    } else if (cmd === "extendParseManage") {
      if (Object.keys(this.extendParse).length === 0) {
        return Message.warning("未获取到地图包内的扩展属性模版, 不做比较");
      }
      let keys1 = Object.keys(this.extendParse);
      let keys2 = Object.keys(extendParseDemo);
      let addList = _.difference(keys2, keys1); //增加
      let deleteList = _.difference(keys1, keys2); //删除
      let updateList = _.difference(keys1, deleteList).filter((key: string) => {
        return (
          JSON.stringify(this.extendParse[key]) !==
          JSON.stringify(extendParseDemo[key])
        );
      }); //修改
      if ([...addList, ...deleteList, ...deleteList].length === 0) {
        return Message.success("地图包内的扩展属性模版与默认的一致, 无需更新");
      }
      // @ts-ignore
      this.$refs.extendManage.intDatas(addList, deleteList, updateList);
      // @ts-ignore
      this.$refs.extendManage.isExtendParseDialog = true;
    } else if (cmd === "toggleSpotVisible") {
      // 点位显示切换
      this.Topo.setLayersVisible("spot", this.Topo.layersVisible["spot"]);
    } else if (cmd === "toggleRouteVisible") {
      // 路径显示切换
      this.Topo.setLayersVisible("route", this.Topo.layersVisible["route"]);
    }
  }
  // 下载或者保存地图文件
  saveFile(cmd: string) {
    if (this.surplusInfo) {
      if (cmd === "sendToScheduler") this.isSaveToSchedulerLoading = true;
      this.$nextTick(() => {
        let newDatas = {
          // @ts-ignore
          originX: _.round(this.Topo.ox / this.Topo.SCALE, 2),
          // @ts-ignore
          originY: _.round(this.Topo.oy / this.Topo.SCALE, 2),
          // @ts-ignore
          width: _.round(this.Topo.width / this.Topo.SCALE, 2),
          // @ts-ignore
          height: _.round(this.Topo.height / this.Topo.SCALE, 2),
          // @ts-ignore
          routes: this.Topo.handleRoutesToFile(
            this.Topo.routesMapper,
            this.extendParse
          ),
          // @ts-ignore
          spots: this.Topo.handleSpotsToFile(
            this.Topo.spotsMapper,
            this.extendParse
          ),
          reflectionPlates: this.Topo.handlePlatesToFile(),
          areas: this.Topo.handleAreasToFile(),
        };
        //this.surplusInfo：包含所有不变的数据;extendParse: 扩展属性解析 this.LaserMap.getLaserData() 包含pgmContent,pgmWidth,pgmHeight
        let datas = Object.assign(
          this.surplusInfo,
          { extendParse: this.extendParse },
          newDatas,
          // @ts-ignore
          this.LaserMap.getLaserData()
        );
        // 下载文件
        if (cmd === "downloadZip" || cmd === "downloadPng") {
          // @ts-ignore
          window.handleZip.saveMapFile(datas, cmd);
        } else {
          // @ts-ignore 保存至后台或者调度
          window.handleZip
            .saveMapFile(datas, cmd)
            // @ts-ignore
            .then(({ content, fileName }) => {
              let params = new FormData();
              if (cmd === "saveToServer") {
                // 为了使得传递的文件流中的头部filename为地图名称，直接传递blob的话，filename="blob"
                params.append(
                  "file",
                  new File([content], fileName, { type: "application/zip" })
                );
                if (this.serverCheckedMap.id) {
                  let fun = () => {
                    params.append("id", this.serverCheckedMap.id);
                    saveMapToServer(params).then((res: any) => {
                      if (!res || res.code !== 200) return;
                      // 地图修改成功后，保存上次修改的信息
                      saveBeginData(
                        `serverMap${this.serverCheckedMap.id}`,
                        content
                      );
                      Message.success("后台地图修改成功");
                    });
                  };
                  // 与后台的地图md5做对比
                  getMapDetailsFromServer({
                    id: this.serverCheckedMap.id,
                  }).then((res: any) => {
                    if (!res) return;
                    handleNewData(
                      `serverMap${this.serverCheckedMap.id}`,
                      res,
                      fun
                    );
                  });
                } else {
                  let fun = () => {
                    // 保存地图至后台
                    saveMapToServer(params).then((res: any) => {
                      if (!res || res.code !== 200) return;
                      Message.success("保存地图至后台成功");
                    });
                  };
                  getMapListFromServer()
                    .then((res: any) => {
                      if (res && res.data && res.data.length > 0) {
                        let filter = res.data.filter((item: any) => {
                          return item.name === fileName;
                        });
                        if (filter.length > 0) {
                          MessageBox.confirm(
                            `后台已存在同名地图, 确定覆盖?`,
                            "提示",
                            {
                              confirmButtonText: "确定",
                              cancelButtonText: "取消",
                              type: "warning",
                            }
                          )
                            .then(() => {
                              params.append("id", filter[0].id);
                              saveMapToServer(params).then((res: any) => {
                                if (!res || res.code !== 200) return;
                                Message.success("后台地图修改成功");
                              });
                            })
                            .catch(() => {});
                        } else {
                          fun();
                        }
                      } else {
                        fun();
                      }
                    })
                    .catch(() => {
                      fun();
                    });
                }
              } else if (cmd === "sendToScheduler") {
                params.append(
                  "file",
                  new File([content], fileName + ".zip", {
                    type: "application/zip",
                  })
                );
                let areaName =
                  this.schedulerAreaList[this.schedulerCheckedArea].name;
                saveMapToScheduler(
                  params,
                  Number(this.schedulerCheckedArea),
                  areaName
                ).then((res: any) => {
                  this.isSaveToSchedulerLoading = false;
                  if (res.state || res.storeSuccess) {
                    this.saveToScheduler = false;
                    saveBeginData(`scheduler${areaName + fileName}`, content); //保存上次修改的数据
                    // 新增或修改调度地图后，会自动将该地图禁用，需要重新启用
                    MessageBox.confirm(
                      `地图保存至调度成功, 是否启用该地图?`,
                      "提示",
                      {
                        confirmButtonText: "确定",
                        cancelButtonText: "取消",
                        type: "warning",
                      }
                    )
                      .then(() => {
                        // 获取调度地图详情，用作启用地图
                        getMapListFromScheduler().then((res: any) => {
                          if (res.state) {
                            let map = res.data.data.filter(
                              (item: any) =>
                                item.name === fileName &&
                                item.areaId === this.schedulerCheckedArea
                            )[0];
                            if (map) {
                              map.enable = 1;
                              updateSchedulerMap(map).then((res: any) => {
                                if (res.state) {
                                  Message.success(`地图启用成功`);
                                }
                              });
                            } else {
                              Message.error("在调度地图列表中找不到该地图");
                            }
                          }
                        });
                      })
                      .catch(() => {});
                  }
                });
              }
            });
        }
      });
    } else {
      Message.warning("请先选择一张地图并打开");
    }
  }
  // 获取后台地图设备列表
  getServerMapList() {
    this.isShowDrawer = true;
    this.serverMapList = [];
    getMapListFromServer().then((res: any) => {
      if (res && res.data && res.data.length > 0) {
        this.serverMapList = res.data;
        // 选中之前打开的地图
        if (this.serverCheckedMap.id) {
          this.serverMapList.some((item: any, i: number) => {
            if (item.id === this.serverCheckedMap.id) {
              // @ts-ignore
              this.$refs.serverTable.setCurrentRow(this.serverMapList[i]);
              return;
            }
          });
        }
      }
    });
  }
  // 打开后台地图
  openServerMap(val: any) {
    this.currentRow = val;
    if (val && this.serverCheckedMap.id !== val.id) {
      this.isGetMapDetailsLoading = true;
      // 打开选中的地图
      getMapDetailsFromServer({
        id: val.id,
      }).then((res: any) => {
        this.isGetMapDetailsLoading = false;
        if (!res) return;
        saveBeginData(`serverMap${val.id}`, res); //保存初始数据
        // @ts-ignore
        let name = val.name;
        let file = new File([new Blob([res])], name + ".zip", {
          type: "application/zip",
        });
        let e = { target: { files: [file] } };
        this.readMapFile(e, "server");
      });
    }
  }
  // 删除后台地图
  deleteServerMap(id: number, name: string) {
    this.$confirm(`确定删除 ${name}?`, "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning",
    })
      .then(() => {
        deleteServerMap({ id }).then((res: any) => {
          if (!res || res.code !== 200) return;
          Message.success("后台地图删除成功");
          this.getServerMapList();
        });
      })
      .catch(() => {});
  }
  // 解析地图压缩包
  readMapFile(e: Event | any, type?: any) {
    this.loadingMessage && this.loadingMessage.close();
    this.loadingMessage = this.$message({
      type: "info",
      duration: 200000,
      message: "正在绘制地图中, 请稍后",
    });
    // @ts-ignore
    window.handleZip
      .parseMapFile(e, {})
      .then(async (res: any) => {
        let file = document.getElementById("filepicker");
        //@ts-ignore 每次打开本地文件后，清空选择，以便于下次打开同样的文件，也能触发函数
        file.value = "";
        let {
          width,
          height,
          routes: lines,
          spots,
          reflectionPlates,
          areas,
          originX,
          originY,
          pgmContent,
          extendParse,
          ...surplusInfo
        } = res;
        if (extendParse) {
          this.extendParse = extendParse; //保存json文件中的扩展属性解析
        } else {
          this.extendParse = extendParseDemo;
        }
        this.surplusInfo = surplusInfo; //将不变的数据存放
        // 从调度获取外部设备列表，体现在地图编辑页面中
        if (type === "scheduler") {
          // 获取外部设备列表
          let externalMapper: any = {};
          let externalListRes = await getExternalList({
            mapIds: [this.schedulerMap.selectedMap],
          });
          if (externalListRes && externalListRes.state) {
            externalListRes.data.data.forEach((external: any) => {
              // 返回的点位由逗号拼接，需要切割；适配旧版本vertexCode
              let vertexCodes = external.vertexCodes || external.vertexCode;
              vertexCodes = vertexCodes.split(",");
              vertexCodes.forEach((code: string) => {
                externalMapper[code] = external;
              });
            });
          }
          spots = spots.map((spot: any) => {
            let external = externalMapper[spot.code];
            if (external) spot.externalType = external.typeId;
            return spot;
          });
        }
        let conf = {
          width,
          height,
          lines,
          spots: this.Topo.handleSpotsFromFile(spots),
          reflectionPlates: reflectionPlates || [],
          areas: areas || [],
          originX,
          originY,
        };
        Object.assign(conf, {
          SCALE: 20,
          ele: document.getElementById("main"),
        });
        //@ts-ignore
        this.Topo.renderMapData(conf);
        //@ts-ignore
        this.Topo.render();
        //@ts-ignore
        this.LaserMap.renderMapCloud(pgmContent);
        this.thumbnailCanvas = new thumbnailCanvas(conf); // 增加缩略图
        // this.timeoutControl.init(); // 开始计时
        // 清空后台和调度地图选中项
        if (type === "server") {
          this.serverCheckedMap = JSON.parse(JSON.stringify(this.currentRow));
          this.schedulerCheckedArea = ""; //清空调度地图选中项
          this.schedulerCheckedMap = 0;
          this.isGetMapDetailsLoading = false;
          //@ts-ignore
          this.$refs.serverDrawer.closeDrawer(); //关闭后台地图列表抽屉
        } else if (type === "scheduler") {
          this.serverCheckedMap = {}; //清空后台地图选中项
          this.schedulerCheckedMap = this.schedulerMap.selectedMap;
          this.isGetMapDetailsLoading = false;
          this.openFromScheduler = false;
        } else {
          this.serverCheckedMap = {};
          this.schedulerCheckedArea = "";
          this.schedulerCheckedMap = 0;
        }
        this.loadingMessage && this.loadingMessage.close();
      })
      .catch((err: any) => {
        this.isGetMapDetailsLoading = false;
        // 只能选择zip格式的压缩包
        Message.error("解析文件失败：" + err);
        this.loadingMessage && this.loadingMessage.close();
      });
  }
  // 当选中的区域发生变化时
  filterSchedulerMaps() {
    this.schedulerMap.selectedMap = "";
  }
  // 获取调度区域列表
  getSchedulerAreaList() {
    getAreaListFromScheduler().then((res: any) => {
      if (res.state && res.data.data) {
        this.schedulerAreaList = {};
        res.data.data.forEach((item: any) => {
          this.$set(this.schedulerAreaList, item.id, item);
        });
      }
    });
  }
  // 从调度获取地图列表
  getSchedulerMapList() {
    this.isGetMapListLoading = true;
    this.getSchedulerAreaList();
    // 获取调度地图列表
    getMapListFromScheduler().then((res: any) => {
      this.isGetMapListLoading = false;
      if (res.state) {
        this.schedulerMap.schedulerMaps.clear();
        let data = res.data.data;
        if (data && data.length > 0) {
          data.forEach((item: any) => {
            this.schedulerMap.schedulerMaps.set(item.id, item);
          });
          if (this.schedulerMap.schedulerMaps.get(this.schedulerCheckedMap)) {
            this.schedulerMap.selectedMap = this.schedulerCheckedMap;
          } else {
            this.schedulerMap.selectedMap = "";
          }
        }
      } else {
        this.openFromScheduler = true;
      }
    });
  }
  // 打开选择的调度地图
  openSchedulerMap() {
    if (!this.schedulerMap.selectedMap) return Message.warning("请选择地图");
    if (this.schedulerCheckedMap !== this.schedulerMap.selectedMap) {
      this.isGetMapDetailsLoading = true;
      let fileName = this.schedulerMap.schedulerMaps.get(
        this.schedulerMap.selectedMap
      ).name;
      let areaName = this.schedulerAreaList[this.schedulerCheckedArea].name;
      getMapDetailsFromScheduler(
        fileName,
        Number(this.schedulerCheckedArea),
        areaName
      ).then((res: any) => {
        this.isGetMapDetailsLoading = false;
        if (!res) return;
        saveBeginData(`scheduler${areaName + fileName}`, res); //保存初始数据
        let file = new File([new Blob([res])], fileName + ".zip", {
          type: "application/zip",
        });
        let e = { target: { files: [file] } };
        this.readMapFile(e, "scheduler");
      });
    } else {
      this.openFromScheduler = false;
    }
  }
  // 将地图保存至调度
  saveMapToScheduler() {
    if (!this.schedulerCheckedArea) return Message.warning("请选择区域");
    let fun = () => {
      this.saveFile("sendToScheduler");
    };
    if (
      this.schedulerCheckedMap &&
      this.schedulerMap.schedulerMaps.get(this.schedulerCheckedMap) &&
      this.schedulerAreaList[this.schedulerCheckedArea]
    ) {
      // 如果是从调度打开的地图，保存前做与调度地图的md5对比
      let fileName = this.schedulerMap.schedulerMaps.get(
        this.schedulerCheckedMap
      ).name;
      let areaName = this.schedulerAreaList[this.schedulerCheckedArea].name;
      getMapDetailsFromScheduler(
        fileName,
        Number(this.schedulerCheckedArea),
        areaName
      ).then((res: any) => {
        if (!res) return;
        handleNewData(`scheduler${areaName + fileName}`, res, fun);
      });
    } else {
      fun();
    }
  }
  handleUndefinedValue(template: any) {
    let currentKey = "";
    for (var key in template) {
      if (template[key] === undefined) {
        template[key] = -1;
        currentKey = key;
        this.$nextTick(() => {
          template[currentKey] = 0;
        });
      }
    }
    return template;
  }
  // 准备填充
  addFillContent() {
    this.Topo.addFillContent();
  }
  // 搜索点位
  handleSelectValueChange() {
    if (this.mapSelectValue) {
      if (typeof this.mapSelectValue === "string") {
        let type = this.mapSelectValue.split("spotType--")[1];
        this.Topo.handleSelectValueChange(Number(type), true);
      } else {
        this.Topo.handleSelectValueChange(this.mapSelectValue, false);
      }
    }
  }
  // 当打开点位下拉选择框时，通过显隐切换，来实现spotsMapper的双向绑定
  responseSpotsMapperChange(isSelect: boolean) {
    if (isSelect) {
      this.mapSelectValue = "";
      this.isMapSelectShow = false;
      this.isMapSelectShow = true;
    }
  }
  //打开批量修改弹框
  openBatchDialog() {
    this.batchForm.spot = JSON.parse(
      JSON.stringify(this.Topo.spotProps.propertyTemplate)
    );
    this.batchForm.route = JSON.parse(
      JSON.stringify(this.Topo.routeProps.propertyTemplate)
    );
    let extendUser = this.extendUser;
    this.extendUser = "spot";
    this.batchForm.spotExtend = this.handleExtend("");
    this.extendUser = "route";
    this.batchForm.routeExtend = this.handleExtend("");
    this.extendUser = extendUser;
    this.isBatch = true;
  }
  // 批量修改所有路径
  handleBatch(key: string, type: string, name: string) {
    this.$confirm(`确定批量修改: ${name}?`, "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning",
    })
      .then(() => {
        switch (type) {
          case "spot": //批量修改点位属性
          case "spotExtend":
            // eslint-disable-next-line no-case-declarations
            let spots: any =
              this.batchForm.spotsRange === "check"
                ? this.Topo.handlerSpots
                : this.Topo.spotsMapper;
            spots.forEach((spot: any) => {
              if (type === "spot") {
                // @ts-ignore 批量修改点位原生属性
                spot[key] = handleBatchValue(key, this.batchForm.spot[key]);
              } else {
                // 批量修改点位扩展属性
                let extend = this.handleExtend(spot.extend);
                // @ts-ignore
                extend[key] = this.batchForm.spotExtend[key];
                let json = this.handleExtendJSON(extend);
                spot.extend = JSON.stringify(json);
              }
              this.Topo.updateSpot(spot, true);
            });
            break;
          case "route": //批量修改路径属性
          case "routeExtend":
            // eslint-disable-next-line no-case-declarations
            let routes: any =
              this.batchForm.routesRange === "check"
                ? this.Topo.handlerRoutes
                : this.Topo.routesMapper;
            routes.forEach((route: any) => {
              let line = this.Topo.routesMapper.get(
                `${route.startSpotRefId}-${route.endSpotRefId}`
              );
              if (line) {
                if (type === "route") {
                  // @ts-ignore 批量修改路径原生属性
                  line[key] = handleBatchValue(key, this.batchForm.route[key]);
                  // 批量修改路径转弯方式时，需要重新绘制路径箭头
                  if (key === "property3") {
                    this.Topo.updateRoute(
                      this.Topo.routesMapper.get(
                        `${line.startSpotRefId}-${line.endSpotRefId}`
                      ),
                      line[key]
                    );
                  }
                } else {
                  //批量修改路径扩展属性
                  let extend = this.handleExtend(route.extend);
                  // @ts-ignore
                  extend[key] = this.batchForm.routeExtend[key];
                  let json = this.handleExtendJSON(extend);
                  route.extend = JSON.stringify(json);
                }
                Topology.instance.routesMapper.set(
                  `${route.startSpotRefId}-${route.endSpotRefId}`,
                  line
                );
              }
            });
            break;
        }
        Message.success("批量修改成功");
      })
      .catch(() => {});
  }
  // 处理扩展数据
  handleExtend(extend: any) {
    // 如果从地图获取到的数据不是JSON字符串
    if (!isJson(extend)) {
      extend = {};
      for (let key in this.extendParse) {
        let item = this.extendParse[key];
        if (item.user === this.extendUser) {
          if (typeof item.name === "object") {
            if (
              typeof item.value === "object" &&
              item.value.length === item.name.length
            ) {
              extend[key] = JSON.parse(JSON.stringify(item.value));
            } else {
              extend[key] = new Array(item.name.length);
            }
          } else {
            extend[key] = item.value;
          }
        }
      }
    } else {
      // 已加过扩展属性
      extend = JSON.parse(String(extend));
      // 如果有key不存在切value是个数组，则往里塞
      for (let key in this.extendParse) {
        let item = this.extendParse[key];
        if (item.user === this.extendUser && !extend[key]) {
          if (typeof item.name === "object") {
            if (
              typeof item.value === "object" &&
              item.value.length === item.name.length
            ) {
              extend[key] = JSON.parse(JSON.stringify(item.value));
            } else {
              extend[key] = new Array(item.name.length);
            }
          }
        }
      }
    }
    return extend;
  }
  // 将布尔值清空
  changeBoolean(obj: any, key: any) {
    obj[key] = null;
  }
  // 移除数值为空的key
  handleExtendJSON(data: any) {
    let json: any = {};
    for (var key in data) {
      let value = data[key];
      if (value || value === false || value === 0) {
        if (typeof value === "object") {
          if (value.length > 0) {
            let result = value.filter((i: any) => {
              return i || i === 0;
            });
            // 只有当数组里的值都是空，移除key
            if (result.length > 0) {
              json[key] = JSON.parse(JSON.stringify(value));
            }
          }
        } else {
          json[key] = value;
        }
      }
    }
    return json;
  }
  // 更新地图包内的扩展属性模版
  changeExtendParse(list: any) {
    list.forEach(({ type, value }: any) => {
      if (type === 1) {
        delete this.extendParse[value]; //删除
      } else {
        this.$set(this.extendParse, value, extendParseDemo[value]); //修改或者增加
      }
    });
    Message.success("地图扩展属性模版更新成功");
    this.closePropTemplate(); //关闭右侧属性弹框，以免看到错误信息（增加的不会被展示，需要重新打开）
  }
}
