import Vue from 'vue'

// 密码验证或者二次确认
export const passwordValidation = async (title) => {
    let result;
    if (window.globalConfig.needPassword) {
        await Vue.prototype.$prompt("请输入密码", "密码", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            inputValidator: (msg) => {
                // @ts-ignore
                return msg === String(window.globalConfig.password)
            },
            inputErrorMessage: "密码不正确, 请重试"
        })
            .then(() => { result = true })
            .catch(() => { result = false });
    } else {
        if (!title) return true; // 不需要二次确认
        await Vue.prototype.$confirm(`确定${title}?`, "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
        }).then(() => { result = true }).catch(() => { result = false });
    }
    return result
}

// 随机颜色生成的，将颜色转化为rgb格式
export const getRGB = (color) => {
    return "rgb(" +
        Math.floor(color[0]) +
        "," +
        Math.floor(color[1]) +
        "," +
        Math.floor(color[2]) +
        ")"
}