import { RouteInterface } from "@/lib/types";
import SVG from "svg.js";

class Route {

    public readonly refId: number = 0;
    public readonly startSpotRefId: number = 0;
    public readonly endSpotRefId: number = 0;

    icon?: SVG.G | null = null;
    arrow?: SVG.Marker | null = null
    maxSpeed: number = 0;
    avoidanceMode: number = 0;
    expansionMode: number = 0;
    fullWeight: number = 0;
    isAvoidance: number = 0;
    pointMode: number;
    type: number = 0;
    walkMode: number;
    walkConfidence: number;
    property1: string;
    property2: number;
    property3: number;
    property4: string;
    property5: number = 0;
    securityDist: number;
    extend: string

    constructor(route: RouteInterface) {
        this.refId = route.refId;
        this.startSpotRefId = route.startSpotRefId;
        this.endSpotRefId = route.endSpotRefId;
        this.maxSpeed = route.maxSpeed;
        this.avoidanceMode = route.avoidanceMode;
        this.expansionMode = route.expansionMode;
        this.fullWeight = route.fullWeight;
        this.isAvoidance = route.isAvoidance;
        this.pointMode = Number(route.pointMode);
        this.property1 = route.property1;
        this.property2 = Number(route.property2);
        this.property3 = Number(route.property3);
        this.property4 = route.property4;
        this.property5 = Number(route.property5);
        this.securityDist = route.securityDist;
        this.walkConfidence = route.walkConfidence;
        this.walkMode = route.walkMode;
        this.extend = route.extend;
    }
}

export const getRoutesCopyData = (arr: Route[]) => {
    return arr.map(route => {
        return {
            refId: route.refId,
            startSpotRefId: route.startSpotRefId,
            endSpotRefId: route.endSpotRefId,
            maxSpeed: route.maxSpeed,
            avoidanceMode: route.avoidanceMode,
            expansionMode: route.expansionMode,
            fullWeight: route.fullWeight,
            isAvoidance: route.isAvoidance,
            pointMode: route.pointMode,
            property1: route.property1,
            property2: Number(route.property2),
            property3: Number(route.property3),
            property4: route.property4,
            property5: Number(route.property5),
            securityDist: route.securityDist,
            walkConfidence: route.walkConfidence,
            walkMode: route.walkMode,
            extend: route.extend
        }
    })
};


export default Route
