<template>
  <el-dialog
    title="更新扩展属性模版"
    :visible.sync="isExtendParseDialog"
    width="600px"
  >
    <el-tree
      class="tree-filter"
      :data="datas"
      node-key="id"
      ref="tree"
      :default-expand-all="true"
      show-checkbox
      @check="handleCheckChange"
      :default-checked-keys="defaultCheckedList"
    >
      <div
        slot-scope="{ data, node }"
        :class="{
          addColor:
            node.parent.label === '增加' &&
            defaultCheckedList.indexOf(data.id) !== -1,
          updateColor:
            node.parent.label === '修改' &&
            defaultCheckedList.indexOf(data.id) !== -1,
          deleteColor:
            node.parent.label === '删除' &&
            defaultCheckedList.indexOf(data.id) !== -1,
        }"
        class="node-class"
      >
        {{ data.label }}
      </div>
    </el-tree>
    <div slot="footer">
      <el-button @click="isExtendParseDialog = false">取 消</el-button>
      <el-button type="primary" @click="saveEdit">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
const extendParseDemo = require("../../../../public/static/extendParseDemo.json");
export default {
  props: ["extendParse"],
  data() {
    return {
      datas: [],
      defaultCheckedList: [], //默认选中
      isExtendParseDialog: false,
      keyMap: {},
    };
  },
  methods: {
    // 选取的节点发生变化时，保存所有选中节点的id
    handleCheckChange(data, { checkedKeys }) {
      this.defaultCheckedList = checkedKeys;
    },
    // 获取对应的name
    getName(label) {
      return extendParseDemo[label]
        ? extendParseDemo[label].name
        : this.extendParse[label].name;
    },
    // 初始化数结构数据
    intDatas(addList, deleteList, updateList) {
      let id = 1;
      this.datas = [
        {
          label: "默认扩展属性模版与地图内的对比",
          children: [
            {
              label: "增加",
              children: [],
            },
            {
              label: "删除",
              children: [],
            },
            {
              label: "修改",
              children: [],
            },
          ],
        },
      ];
      this.defaultCheckedList = [];
      [addList, deleteList, updateList].forEach((item, i) => {
        item.forEach((value, j) => {
          this.defaultCheckedList.push(id);
          this.keyMap[id] = { value, type: i };
          this.datas[0].children[i].children[j] = {
            label: this.getName(value),
            value: value,
            id: id++,
          };
        });
      });
    },
    // 保存修改
    saveEdit() {
      let list = [];
      for (var key in this.keyMap) {
        if (this.defaultCheckedList.indexOf(Number(key)) > -1) {
          list.push(this.keyMap[key]);
        }
      }
      // 只有勾选的数量大于0，才去修改
      if (list.length > 0) {
        this.$emit("changeExtendParse", list);
      }
      this.isExtendParseDialog = false;
    },
  },
};
</script>

<style scoped>
.tree-node {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding-right: 20px;
  line-height: 40px;
}
.uploadDialog /deep/ .el-dialog {
  width: 400px;
}
.node-class {
  padding: 0 5px;
  font-size: 16px;
  font-weight: 500;
}
.addColor {
  background-color: #67c23a;
  color: #fff;
}
.updateColor {
  background-color: #e6a23c;
  color: #fff;
}
.deleteColor {
  background-color: #f56c6c;
  color: #fff;
}
</style>