import LaserMap from "@/lib/core/laser-map";
import Topology from "@/lib/core/topo";
import _ from "lodash";
import { lineLength } from "@/lib/utils/length";
import { vectorAngle } from "@/lib/utils/rotate-angle";
import Spot from "@/lib/core/spot";
import Route from "@/lib/core/route";
import { PRECISION } from "@/lib/common";

/**
 * dom 控制合并的功能 DOM 元素
 * 如 angle input element
 * 取消合并按钮 button
 * 确认合并按钮 button
 * 锁定旋转状态按钮 button
 *
 * visible 是否显示 dom
 *
 * angle 旋转角度
 * rotatable 是否允许旋转操作
 * lock 锁定旋转
 * cancel 取消合并
 * confirm 提交合并
 * addCustomMergeUI 外部合并地图上传文件入口
 * renderMergeData  渲染被合并地图数据
 */
export interface mergeTemplateInterface {
    dom: HTMLElement;
    visible: boolean;
    angle: number;
    rotatable: boolean;
    lock: Function;
    cancel: Function;
    confirm: Function;
    addCustomMergeUI: Function;
    renderMergeData: Function;
}

const LOCKICON = require('./assets/lock.png');
const UNLOCKICON = require('./assets/unlock.png');

class MergeMap {
    private dom: HTMLElement | null = null;

    // 保存合并前的菜单状态，合并完成后需要恢复
    private status: number | number[] = 0;

    // 带合并的 激光数据
    cvs: HTMLCanvasElement | null = null;
    ctx: CanvasRenderingContext2D | null = null;
    // 待合并的 TOPO 数据集合
    tpData = {
        spots: <any>[],
        lines: <any>[]
    };
    // 数据显示画布
    layerCvs: HTMLCanvasElement | null = null;
    layerCtx: CanvasRenderingContext2D | null = null;


    // 正方形尺寸的画布 (矩形对角线)，用来获取 cvs 旋转变换后的数据内容
    squareCvs: HTMLCanvasElement | null = null;
    squareCtx: CanvasRenderingContext2D | null = null;
    // squareCvs 和 cvs 画布坐标偏移量 dx dy
    dx: number = 0;
    dy: number = 0;

    // 角度
    angle: number = 0;

    rotatable: boolean = false;
    /**
     * 画布中的偏移量 无缩放
     */
    mergeX = 0;
    mergeY = 0;

    /**
     * 要合并的最终【已计算的】数据 含 Topo数据 和 激光数据 ag 是为了匹配旋转后的矩形区域激光数据
     */
    // mergeData: { ag: number, data: { x: number, y: number, width: number, height: number }, tp: Spot | Route [] }[] = [];

    private _visible = false;
    private btn1: HTMLElement | null = null;
    public pgmContent: any = null

    constructor() {
        /**
         * DOM 容器
         */
        let box = document.createElement('div');
        box.className = 'merge-ctl-box';

        /**
         * 激光数据
         */
        this.cvs = document.createElement('canvas');
        this.ctx = this.cvs.getContext('2d');
        /**
         * 显示数据层
         */
        this.layerCvs = document.createElement('canvas');
        this.layerCtx = this.cvs.getContext('2d');

        /**
         * 过渡图层
         */
        this.squareCvs = document.createElement('canvas');
        this.squareCtx = this.squareCvs.getContext('2d');

        /**
         * input
         */
        let input = document.createElement('input');
        input.value = String(this.angle);
        input.onchange = this.angleChange.bind(this);
        input.type = 'number';
        input.step = '0.5';
        input.min = '-180';
        input.max = '180';
        box.append(input);
        /**
         * button
         */
        let btn1 = document.createElement('button');
        this.btn1 = btn1;
        btn1.className = 'lock';
        btn1.innerHTML = `<img src="${UNLOCKICON}">`;
        // @ts-ignore
        btn1.onclick = this.lock.bind(this);
        box.append(btn1);
        let btn2 = document.createElement('button');
        // @ts-ignore
        btn2.onclick = this.cancel.bind(this);
        btn2.className = 'cancel';
        btn2.innerText = '取消';
        box.append(btn2);
        let btn3 = document.createElement('button');
        // @ts-ignore
        btn3.onclick = this.confirm.bind(this);
        btn3.className = 'confirm';
        btn3.innerText = '确认';
        box.append(btn3);

        // 避免干扰窗口编辑定位
        [
            // 'mousemove',
            // 'mouseover',
            // 'mouseout',
            'mousedown',
            // 'mouseup'
        ].forEach(v => {
            box.addEventListener(v, (e) => {
                // e.preventDefault();
                e.stopPropagation();
                return false;
            });
        });

        this.dom = box;
        // dom 挂载对象需修改
        // TODO
        // @ts-ignore
        document.getElementById('main').append(box);
    }

    // 暴露外部覆盖
    addCustomMergeUI() {
    }

    get visible(): boolean {
        return this._visible
    }

    set visible(val: boolean) {
        this._visible = val;
        // @ts-ignore
        this.dom.style.display = val ? 'block' : 'none';
    }

    renderMergeData(res: any) {
        this.visible = true;
        // 保存
        this.status = Topology.instance.state;
        Topology.instance.state = 0;
        this.mergeX = this.mergeY = 0;
        this.angle = 0;
        this.rotatable = false;
        // @ts-ignore
        this.btn1.innerHTML = `<img src="${UNLOCKICON}">`;
        this.tpData.lines = res['lines'];
        this.tpData.spots = []; // 需重置，后续坐标转化后会生成新数据
        // @ts-ignore
        const imgData = window.handleZip.correctImageData(res.pgm);
        let { width, height } = imgData;
        // img.src = process.env.VUE_APP_BASE_API + '/tool/getPNG?pgmid=' + res.pgmId;
        // img.onload = () => {
        //激光原始数据
        // @ts-ignore
        [this.cvs.width, this.cvs.height] = [width, height];
        // @ts-ignore
        this.ctx = this.cvs.getContext('2d');
        this.ctx?.putImageData(imgData, 0, 0);
        this.pgmContent = imgData

        // 变换过渡
        // 图片对角线长度
        let diagonal = _.round(Math.sqrt(width * width + height * height));
        // @ts-ignore
        this.squareCvs.width = this.squareCvs.height = diagonal;
        // @ts-ignore
        this.squareCtx = this.squareCvs.getContext('2d');

        this.dx = (diagonal - width) / 2;
        this.dy = (diagonal - height) / 2;

        //layer
        // @ts-ignore
        [this.layerCvs.width, this.layerCvs.height] = [width, height];
        // @ts-ignore
        this.layerCtx = this.layerCvs.getContext('2d');
        this.layerCtx?.putImageData(imgData, 0, 0);
        let { SCALE } = Topology.instance;
        // @ts-ignore
        this.layerCtx.fillStyle = '#ee880066';
        res['spots'].forEach((spot: any) => {
            let x = _.round((spot.x - res['originX']) * SCALE, PRECISION);
            let y = _.round((res['originY'] + res['height'] - spot.y) * SCALE, PRECISION);
            // 额外添加画布坐标 cvsx cvsy
            spot['cvsx'] = x;
            spot['cvsy'] = y;
            this.tpData.spots.push(spot);
            this.layerCtx?.beginPath();
            this.layerCtx?.arc(x, y, 3, 0, Math.PI * 2);
            this.layerCtx?.closePath();
            this.layerCtx?.fill();
        });
        // @ts-ignore
        this.layerCtx.strokeStyle = '#ee8800ff';
        // @ts-ignore
        this.layerCtx.lineWidth = 1;
        res['lines'].forEach((route: any) => {
            this.layerCtx?.moveTo(_.round((route['startSpotX'] - res['originX']) * SCALE, PRECISION),
                _.round((res['originY'] + res['height'] - route['startSpotY']) * SCALE, PRECISION));
            this.layerCtx?.lineTo(_.round((route['endSpotX'] - res['originX']) * SCALE, PRECISION),
                _.round((res['originY'] + res['height'] - route['endSpotY']) * SCALE, PRECISION));
        });
        this.layerCtx?.stroke();


        // 需要显示待合并拓扑数据

        // 加载完成 屏蔽菜单状态 后续会恢复
        Topology.instance.state = 0;
        LaserMap.instance.renderMergeData(res);
        // };
        // 开始合并时，重置合并数据
        // this.mergeData = [];
    }

    angleChange(e: Event | any) {
        this.angle = Number(e.target.value);
        LaserMap.instance.render();
        console.log('angle', this.angle);
    }

    lock() {
        this.rotatable = !this.rotatable;
        console.log(this.rotatable);
        if (this.rotatable) {
            // @ts-ignore
            this.btn1.innerHTML = `<img src="${LOCKICON}">`;
        } else {
            // @ts-ignore
            this.btn1.innerHTML = `<img src="${UNLOCKICON}">`;
        }
        console.log('lock', this.rotatable)
    }

    cancel() {
        this.visible = false;
        // 恢复菜单栏
        Topology.instance.state = this.status;
        LaserMap.instance.renderMergeData();
        console.log('cancel')
    }

    confirm() {
        this.visible = false;
        // 恢复菜单栏
        Topology.instance.state = this.status;
        LaserMap.instance.renderMergeData();
        console.log('confirm')
    }

    mousedown(e: Event | any) {
        let { gx, gy, scale } = Topology.instance;
        // @ts-ignore
        let { width, height } = LaserMap.instance.mergeCanvas;
        const sx = _.round(gx + this.mergeX + width * scale - 20 * scale);
        const sy = _.round(gy + this.mergeY + height * scale / 2 - 10 * scale);
        const swx = _.round(gx + this.mergeX + width * scale);
        const shy = _.round(gy + this.mergeY + height * scale / 2 + 10 * scale);
        // 以上三个坐标[sx,sy]、[swx,sy]、[sx,shy] 旋转后的坐标
        //[sx,sy]
        const [x, y] = this.rotateComputed([sx, sy]);
        //[swx,sy]
        const [wx, wy] = this.rotateComputed([swx, sy]);
        //[sx,shy]
        const [hx, hy] = this.rotateComputed([sx, shy]);
        //矩形旋转后图形的路径会发生变化，需要判断点击发生在变形后的矩形内  实现方式 向量分解 坐标轴映射
        // [x,y]、[swx,y]、[x,shy]
        const len = lineLength([e.pageX, e.pageY, x, y]);
        const a1 = vectorAngle([e.pageX - x, e.pageY - y]) - vectorAngle([wx - x, wy - y]);
        const v1 = _.round(len * Math.cos(a1 * Math.PI / 180));
        const a2 = vectorAngle([e.pageX - x, e.pageY - y]) - vectorAngle([hx - x, hy - y]);
        const v2 = _.round(len * Math.cos(a2 * Math.PI / 180));
        const l = 20 * scale;
        if (v1 > 0 && v1 < l && v2 > 0 && v2 < l) {
            // this.rotatable = true;
            // @ts-ignore
            Topology.instance.ele.style.cursor = `url(${require('./assets/rotate.png')}) 16 16, auto`;
            console.log('hit');
        } else {
            // this.rotatable = false;
            console.log('miss')
        }
    }

    //浏览器窗口坐标下的点位旋转后的坐标计算公式  激光坐标系需要反转 y Axis 旋转角度取反即可
    rotateComputed(po: [number, number]) {
        let { gx, gy, scale } = Topology.instance;
        // @ts-ignore
        let { width, height } = LaserMap.instance.mergeCanvas;
        //判断点击区域是否是旋转控制区
        const ox = _.round(gx + this.mergeX + width * scale / 2);
        const oy = _.round(gy + this.mergeY + height * scale / 2);
        const x = _.round((po[0] - ox) * Math.cos(this.angle * Math.PI / 180) - (po[1] - oy) * Math.sin(this.angle * Math.PI / 180) + ox);
        const y = _.round((po[0] - ox) * Math.sin(this.angle * Math.PI / 180) + (po[1] - oy) * Math.cos(this.angle * Math.PI / 180) + oy);
        return [x, y]
    }

    /**
     * 旋转绘制
     * @param px pageX
     * @param py pageY
     */
    rotateRender(px: number, py: number) {
        console.log(this)
        // vectorAngle([px - ax, py - ay]) - vectorAngle([LaserMap.instance.mx - ax, LaserMap.instance.my - ay])

    }
}

export default MergeMap

