/**
 * @name: actions
 * @author: chan
 * @date: 2020/8/24 14:50
 * @description：actions  command 具体执行的业务逻辑
 * @update: 2020/8/24 14:50
 */
import { ExecInterface } from "@/lib/core/command/index";
import Topology from "@/lib/core/topo";
import Spot from "@/lib/core/spot";
import Route from "@/lib/core/route";
import LaserMap from "@/lib/core/laser-map";


const undoActions = (exec: ExecInterface) => {
    if (exec.directive === 11) {
        /**
         * UPDATE SPOT
         * [newVal, oldVal]
         */
        exec.target[1].forEach((v: any) => {
            const spot = Topology.instance.spotsMapper.get(v.refId);
            // @ts-ignore
            spot.x = v.x;
            // @ts-ignore
            spot.y = v.y;
            // @ts-ignore
            spot.dir = v.dir;
            // @ts-ignore
            Topology.instance.updateSpot(spot);
            /**
             * 需要同步数据
             */
            //    TODO
        })
    }
    if (exec.directive === 12) {
        /**
         * undo ADD SPOT
         */
        exec.target.forEach(spot => {
            Topology.instance.deleteSpot(spot);
        })
    }
    if (exec.directive === 22) {
        /**
         * undo add route
         */
        exec.target.forEach(route => {
            Topology.instance.deleteRoute(`${route.startSpotRefId}-${route.endSpotRefId}`);
        })
    }
    if (exec.directive === 1212) {
        /**
         * undo add [routes, spots]
         */
        exec.target[0].forEach((route: any) => {
            Topology.instance.deleteRoute(`${route.startSpotRefId}-${route.endSpotRefId}`);
        });
        exec.target[1].forEach((spot: any) => {
            Topology.instance.deleteSpot(spot);
        });
    }
    if (exec.directive === 1323) {
        /**
         * undo delete [routes, spots]
         */
        exec.target[1].forEach((spot: any) => {
            Topology.instance.createSpot(new Spot(spot));
        });
        exec.target[0].forEach((route: any) => {
            Topology.instance.createRoute(new Route(route));
            //优化
            Topology.instance.renderRoute(route.startSpotRefId);
        });
    }
    if (exec.directive === 31 || exec.directive === 32 || exec.directive === 33) {
        /**
         * undo laser map data
         */
        const { x, y, data } = exec.target[1];
        LaserMap.instance.drawLaserData(x, y, data);
    }
    if (exec.directive === 661) {
        /**
         * undo delete plate
         */
        exec.target.forEach(item => {
            Topology.instance.undoPlate(item)
        });
    }
};
export default undoActions
