<template>
<!-- 修改某个点位路径的参数 -->
  <div :class="{ 'prop-template-content': type === 'prop' }">
    <div
      :class="{ 'prop-item': type === 'prop', 'batch-item': type === 'batch' }"
      v-for="(v, k) in form.propertyTemplate"
      :key="k"
      v-show="keyhiddenList.indexOf(k) === -1"
    >
      <span v-if="type === 'prop'"
        ><b v-if="['property2X', 'property3X'].indexOf(k) === -1"
          >{{ form.labels[k] }} :
        </b>
        <el-button
          v-else-if="k === 'property2X'"
          size="mini"
          type="primary"
          @click="Topo.visualSpot(2, item)"
          >{{ form.labels[k] }}</el-button
        >
        <el-button
          v-else
          size="mini"
          type="danger"
          @click="Topo.visualSpot(3, item)"
          >{{ form.labels[k] }}</el-button
        ></span
      >
      <div class="batch-title" v-else-if="type === 'batch'">
        {{ form.labels[k] || form.labels["_" + k] }}:
      </div>
      <span v-if="typeof v === 'boolean'">
        <el-radio v-model="item[k]" :label="true">是</el-radio>
        <el-radio v-model="item[k]" :label="false">否</el-radio>
      </span>
      <span v-if="typeof v === 'string'">
        <el-input
          :class="{ 'prop-form-inline': type === 'prop' }"
          size="mini"
          v-model.trim="item[k]"
        >
        </el-input>
      </span>
      <span v-if="typeof v === 'number'">
        <el-input
          disabled
          size="mini"
          :class="{ 'prop-form-inline': type === 'prop' }"
          :value="
            Topo.spotsMapper.get(item[k])
              ? Topo.spotsMapper.get(item[k]).code
              : ''
          "
          v-if="k.toUpperCase().indexOf('REFID') > -1"
        >
        </el-input>
        <el-input-number
          size="mini"
          :class="{ 'prop-form-inline': type === 'prop' }"
          controls-position="right"
          :min="k === 'maxSpeed' ? 0 : -Infinity"
          :max="k === 'maxSpeed' ? 3 : Infinity"
          v-model.number="item[k]"
          v-else
        >
        </el-input-number>
      </span>
      <span v-if="Array.isArray(v) && (type !== 'batch' || item[k])">
        <el-select
          size="mini"
          :class="{ 'prop-form-inline': type === 'prop' }"
          v-model="item[k][0]"
          placeholder="请选择"
        >
          <el-option
            v-for="(childV, childI) in form.childLabels[k]"
            :key="childI"
            :label="childV"
            :value="childI"
          >
          </el-option>
        </el-select>
      </span>
      <el-button
        v-if="type === 'batch'"
        class="batch-btn"
        type="primary"
        @click="handleBatch(k, batchType, form.labels[k] || form.labels['_' + k])"
        >修改</el-button
      >
    </div>
  </div>
</template>
<script>
export default {
  props: ["form", "keyhiddenList", "Topo", "type", "item", "batchType"],
  methods: {
    handleBatch(key, type, name) {
      this.$emit("handleBatch", key, type, name);
    },
  },
};
</script>

<style lang="sass" scoped>
</style>