import _ from 'lodash';
import { Message } from "element-ui";
import Topology from "@/lib/core/topo";
import { lineAngle } from "@/lib/utils/handle-angle";
import { ICONSIZE, PRECISION } from "@/lib/common";

/*
 ~~ 一个点绕着另一个点旋转一定角度后的坐标
*/
export const transformPoint = (point: { x: number, y: number }, angle: number, basePoint: { x: number, y: number }) => {
    const newPoint = { x: 0, y: 0 }
    newPoint.x = _.round((point.x - basePoint.x) * Math.cos(angle) - (point.y - basePoint.y) * Math.sin(angle) + basePoint.x, 2)
    newPoint.y = _.round((point.x - basePoint.x) * Math.sin(angle) + (point.y - basePoint.y) * Math.cos(angle) + basePoint.y, 2)
    return newPoint
}

/*
 ~~ 获取一条完整路径
*/
export const getCompleteRoute = () => {
    let length = Topology.instance.handlerSpots.length
    if (length > 2) {
        let start = Topology.instance.handlerSpots[0]    //起点
        let end = Topology.instance.handlerSpots[length - 1] //终点
        let routeSpots = [start]  //计算完整路径
        let handlerSpots = Topology.instance.handlerSpots.slice(1,)
        for (let index = 0; index < length - 1; index++) {
            const startSpot = Topology.instance.handlerSpots[index];
            let num = 0;
            let endSpotIndex = 0;
            // 找到连通的下一个点位
            handlerSpots.forEach((endSpot, i) => {
                // 只允许从起点指向终点
                if (Topology.instance.routesMapper.has(`${startSpot.refId}-${endSpot.refId}`)) {
                    if (index !== length - 2 && end.refId === endSpot.refId) return
                    endSpotIndex = i
                    num++;
                }
            })
            // 只有连通的点位有且仅有一个时, 才放入路径中
            if (num === 1) {
                routeSpots.push(handlerSpots[endSpotIndex])
                handlerSpots.splice(endSpotIndex, 1)
            } else {
                Message.warning(`根据已选取的点位列表、起点、终点, 无法构建一条完整路径; 问题点位: ${startSpot.code}`);
                return false
            }
        }
        // 路径的终点必须和选中的终点一致
        if (routeSpots[routeSpots.length - 1].refId !== end.refId) {
            Message.warning(`根据已选取的点位列表、起点、终点, 无法构建一条完整路径; 问题点位: ${routeSpots[routeSpots.length - 1].code}`)
            return false
        } else {
            return { routeSpots, start, end }
        }
    } else {
        Message.warning("选取的点位过少")
        return false
    }
}

/*
 ~~ 根据选中的路径，获取满足条件的所有完整路径
*/
export const getAllCompleteRoutes = () => {
    let route = Topology.instance.handlerRoutes[0]
    if (route) {
        let { startSpotRefId: second, endSpotRefId: third } = route;
        let startList: Array<number> = [];
        let endList: Array<number> = []
        Topology.instance.routesMapper.forEach((route) => {
            let { startSpotRefId, endSpotRefId } = route
            if (endSpotRefId === second && startSpotRefId !== third) {
                startList.push(startSpotRefId)
            } else if (startSpotRefId === third && endSpotRefId !== second) {
                endList.push(endSpotRefId)
            }
        });
        if (!startList.length) {
            Message.warning("选中的路径没有进入的方向, 无法构建一条完整路径")
            return []
        } else if (!endList.length) {
            Message.warning("选中的路径没有离开的方向, 无法构建一条完整路径")
            return []
        } else {
            let params: Array<any> = []
            startList.forEach((start: any) => {
                start = Topology.instance.spotsMapper.get(start)
                endList.forEach((end: any) => {
                    end = Topology.instance.spotsMapper.get(end)
                    params.push({ routeSpots: [start, Topology.instance.spotsMapper.get(second), Topology.instance.spotsMapper.get(third), end], start, end })
                })
            })
            return params
        }
    }
}

/*
 ~~ 获取圆弧数据，准备绘制
*/
export const handleArcPath = ({ routeSpots, start, end }: { routeSpots: any, start: any, end: any }) => {
    let scale = Topology.instance.scale
    // 1. 计算起点和终点与中间两个点位的角度
    const startAngle = Math.atan2(routeSpots[1].y - start.y, routeSpots[1].x - start.x) //计算两点之间的角度
    const endAngle = Math.atan2(end.y - routeSpots[2].y, end.x - routeSpots[2].x)
    let k1 = 0.5, k2 = 0.5;
    // 2. 计算贝塞尔曲线的两个控制点
    let dist = Math.pow(Math.pow(routeSpots[2].y - routeSpots[1].y, 2) + Math.pow(routeSpots[2].x - routeSpots[1].x, 2), 0.5);
    let x1 = dist * k1 * Math.cos(startAngle) + routeSpots[1].x;
    let y1 = dist * k1 * Math.sin(startAngle) + routeSpots[1].y;
    let x2 = dist * k2 * Math.cos(endAngle + Math.PI) + routeSpots[2].x;
    let y2 = dist * k2 * Math.sin(endAngle + Math.PI) + routeSpots[2].y;
    // 3. 返回中间两个点位和控制点位，绘制贝塞尔曲线
    return `M${routeSpots[1].x * scale} ${routeSpots[1].y * scale} C ${x1 * scale} ${y1 * scale}, ${x2 * scale} ${y2 * scale}, ${routeSpots[2].x * scale} ${routeSpots[2].y * scale}`
}

/*
 ~~ 添加路径时的显示效果
*/
export const computedPath = (x1: number, y1: number, x2: number, y2: number) => {
    let scale = Topology.instance.scale
    const ag = lineAngle({ x1, y1, x2, y2 });
    let _x1 = _.round(x1 * scale + ICONSIZE * Math.sin(ag), PRECISION);
    let _y1 = _.round(y1 * scale + ICONSIZE * Math.cos(ag), PRECISION);
    let _x2 = _.round(x2 * scale - ICONSIZE * Math.sin(ag), PRECISION);
    let _y2 = _.round(y2 * scale - ICONSIZE * Math.cos(ag), PRECISION);
    return `M${_x1} ${_y1} L${(_x1 + _x2) / 2} ${(_y1 + _y2) / 2} L${_x2} ${_y2}`;
}

/**
 * 画布坐标转激光坐标值
 */
export const relativeToLaserCoordinate = (rx = 0, ry = 0) => {
    return {
        lx: _.round((Topology.instance.ox + rx) / Topology.instance.SCALE, PRECISION),
        ly: _.round((Topology.instance.oy + Topology.instance.height - ry) / Topology.instance.SCALE, PRECISION)
    }
}

/**
 * 激光坐标转画布坐标值
 */
export const laserToRelativeCoordinate = (lx = 0, ly = 0) => {
    return {
        rx: _.round((lx * Topology.instance.SCALE - Topology.instance.ox), PRECISION),
        ry: _.round((Topology.instance.oy + Topology.instance.height - ly * Topology.instance.SCALE), PRECISION)
    }
}

/**
 *  SVG 画布中的画布坐标转化为窗口坐标
 */
export const relativeToAbsoluteCoordinate = (x = 0, y = 0) => {
    return {
        ax: _.round(x * Topology.instance.scale + Topology.instance.gx + Topology.instance.left, PRECISION),
        ay: _.round(y * Topology.instance.scale + Topology.instance.gy + Topology.instance.top, PRECISION)
    };
}

/**
 * 窗口坐标转化为 SVG 画布中的 laserCanvas 画布坐标
 */
export const absoluteToRelativeCoordinate = (x = 0, y = 0) => {
    return {
        rx: _.round((x - Topology.instance.gx - Topology.instance.left) / Topology.instance.scale, PRECISION),
        ry: _.round((y - Topology.instance.gy - Topology.instance.top) / Topology.instance.scale, PRECISION)
    };
}

/**
 * 输出绝对坐标
 */
export const routePath = (refIds: [number, number] = [0, 0]) => {
    const startSpot = Topology.instance.spotsMapper.get(refIds[0]);
    const endSpot = Topology.instance.spotsMapper.get(refIds[1]);
    const route = {
        x1: startSpot && startSpot.x || 0,
        y1: startSpot && startSpot.y || 0,
        x2: endSpot && endSpot.x || 0,
        y2: endSpot && endSpot.y || 0
    };
    const ag = lineAngle(route);
    /**
     * 双向路径 进行偏移，避免重合
     */
    const reversePathKey = `${refIds[1]}-${refIds[0]}`;
    /**
     * 计算调整后的路径起始点坐标值
     * ★★★★★
     * 缩小到一定程度可能会发生翻转
     * 当路径的长度较小时，采取隐藏的方式规避
     * 需要在窗口坐标系计算长度
     * ★★★★★
     */
    let _x1 = _.round(route.x1 * Topology.instance.scale + ICONSIZE * Math.sin(ag), PRECISION);
    let _y1 = _.round(route.y1 * Topology.instance.scale + ICONSIZE * Math.cos(ag), PRECISION);
    let _x2 = _.round(route.x2 * Topology.instance.scale - ICONSIZE * Math.sin(ag), PRECISION);
    let _y2 = _.round(route.y2 * Topology.instance.scale - ICONSIZE * Math.cos(ag), PRECISION);
    if (Topology.instance.routesMapper.has(reversePathKey)) {
        const offsetAngle = _.round(Math.PI / 2 + ag, PRECISION);
        const offsetX = _.round(ICONSIZE / 4 * Math.sin(offsetAngle), PRECISION);
        const offsetY = _.round(ICONSIZE / 4 * Math.cos(offsetAngle), PRECISION);
        _x1 -= offsetX;
        _y1 -= offsetY;
        _x2 -= offsetX;
        _y2 -= offsetY;
    }
    /**
     * 严格限制 空格  后面会解析
     */
    return `M${_x1} ${_y1} L${(_x1 + _x2) / 2} ${(_y1 + _y2) / 2} L${_x2} ${_y2}`;
}

/**
 * 设置路径箭头颜色，为了区分路径的转弯方式是否是圆弧转弯
 */
export const setArrowColor = (property3: number | string) => {
    return Number(property3) === 0 ? "#f30bca" : "#578cff"
}