/**
 * 核心部分只实现基本的逻辑功能
 * 具体业务执行需要额外完善
 * 理论上，每条指令都需要记录，以待后续的撤销回退
 * 因此，客观上就要求有个统一的拦截器来处理操作指令
 * 因为应用基于与服务器的交互，因此考虑将拦截器放在 ajax 请求前
 */

import Topology from "@/lib/core/topo";
import redoActions from "@/lib/core/command/redo-actions";
import undoActions from "@/lib/core/command/undo-actions";
import {COMMANDSTACKMAX} from "@/lib/common";
import {Message} from "element-ui";

export interface ExecInterface {
    directive: number;
    type: 'UPDATE' | 'ADD' | 'DELETE';
    target: any[]; //[] or [newVal,oldVal]
}


/**
 * 监听 Stack 的变化  初始长度为  0
 */
class commandStack extends Array {
    constructor() {
        // 调用父类Array的constructor()
        super()
    }

    /**
     * 重写方法 只 支持 一个元素参数
     * 对应的是 redo 指令的具体动作
     * 通过 execute 新添加的不需要监听，可以避开与 undo 和 redo 操作的逻辑重合
     * @param args
     */
    push(exec: ExecInterface) {
        // console.log('Stack push 的数据是', exec);
        redoActions(exec);
        return super.push(exec)
    }

    /**
     * 对应的是 undo 指令的具体动作
     */
    pop() {
        const exec = super.pop();
        // console.log('Stack pop 的数据是', exec);
        undoActions(exec);
        return exec;
    }
}


const Command = {
    /**
     * undo stack
     */
    backStack: new commandStack(),

    /**
     * redo stack
     * 唯一来源就是 undo pop 操作的 数据
     */
    goStack: <any>[],

    /**
     * 撤销历史记录最长保持 10 条 ？
     * 需要根据 新 操作不断地 push 新数据 && 清空 redo stack
     * @param exec
     */
    execute(exec: ExecInterface) {
        /**
         * 避开 push 方法添加到最后一个
         */
        this.backStack.splice(this.backStack.length, 0, exec);
        Topology.instance.saveMsg('上次操作' + exec.type + exec.directive);
        const len = this.backStack.length;
        if (len > COMMANDSTACKMAX) {
            this.backStack.splice(0, len - COMMANDSTACKMAX)
        }
        this.goStack = [];
        console.log(this.backStack)
    },

    /**
     * undo func
     */
    undo() {
        if (this.backStack.length > 0) {
            const exec = this.backStack.pop();
            if (exec) {
                this.goStack.push(exec);
                Topology.instance.saveMsg('撤销' + exec.type + exec.directive);
            }
        } else {
            Message.info('无撤销的数据')
        }
    },

    /**
     * redo func
     */
    redo() {
        if (this.goStack.length > 0) {
            const exec = this.goStack.pop();
            if (exec) {
                this.backStack.push(exec);
                Topology.instance.saveMsg('恢复' + exec.type + exec.directive)
            }
        } else {
            Message.info('无恢复的数据')
        }
    }

};

export default Command




