/**
 * 地图 spot 到 prop 编辑表单的字段映射
 */

export const copySpotProps = (spot: Spot) => {
    let props = {};
    for (let k in SpotLabelEnum) {
        // @ts-ignore
        spot[k] !== undefined && (props[k] = spot[k]);
    }
    // @ts-ignore
    props['type'] = [spot['type']];
    // @ts-ignore
    props['orient'] = [spot['orient']];
    // @ts-ignore
    props['property4'] = [spot['property4']]
    // @ts-ignore
    props['property5'] = Boolean(spot['property5']);
    // @ts-ignore
    props['layingOff'] = Boolean(spot['layingOff']);
    // @ts-ignore
    props['isTraffic'] = Boolean(spot['isTraffic']);
    // @ts-ignore
    props['isForbidden'] = Boolean(spot['isForbidden']);
    // @ts-ignore
    props.extend = spot.extend
    return props
};
